/* Auth.Js */

import axios from 'axios';
import React, { useState } from 'react';
import LocalStorageService from './assets/js/utils/LocalStorageService';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/custom.css';

function Auth (props) {

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    function login() {

        setLoading(true);
        const storeAuthentication = (response) => {
            LocalStorageService.setRefreshToken(
                response.data.refresh);
            LocalStorageService.setAccessToken(
                response.data.access);
            LocalStorageService.setUser(
                response.data.user);
            setLoading(false);
        };

        if (username === '' || password === '') {
            setError('Please fill the authentication fields!');
            setLoading(false);
        } else {
            axios
                .post ('http://sancaliar82.pythonanywhere.com/auth/obtain-token',
                    {username: username, password: password})
                .then (response => {storeAuthentication(response);
                    setUsername(''); setPassword('');
                    props.history.push('/');
                })
                .catch (error => {setError('Invalid credentials!');
                    setLoading(false);
                })
        }
    };

    return (
        <div className="bg-gradient-light container-fluid py-5">
            <div className="row justify-content-center">

                <div className="col-md-8">
                    <div className="card shadow border-0 bg-light o-hidden">
                        <div className="card-header bg-square-midnight font-weight-bold text-white py-2">

                            <div className="d-flex flex-row align-items-baseline float-right">
                                <div className="d-inline display-2"><i className="fas fa-th"></i></div>
                                <div className="d-inline h3 mx-3">Smart Cabinet <sup>20</sup></div>
                            </div>
                                
                        </div>

                        <div className="card-body px-5">
                            <div className="d-flex flex-row align-items-center">
                                
                                <div className="col-md-9 mx-auto p-4">
                                    <div className="card shadow border-0 bg-midnight o-hidden">
                                        <div className="card-body p-4">
                                            
                                            <h4 className="card-title text-white px-2">User Authentication</h4>
                                            <div className="col px-5 mx-auto">

                                                {
                                                    error &&
                                                    <div className="alert alert-warning alert-dismissible fade show mb-2" role="alert">
                                                        <span className="small font-weight-bold text-monospace">{error}</span>
                                                        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                                            <span aria-hidden="true">&times;</span>
                                                        </button>
                                                    </div>
                                                }

                                                <form className="user">
                                                    <div className="form-group mb-1">
                                                        <label className="small font-weight-bold text-white">Username:</label>
                                                        <input className="form-control text-monospace" type="text" value={username} onChange={
                                                            (e)=>setUsername (e.target.value)} required/>
                                                    </div>

                                                    <div className="form-group">
                                                        <label className="small font-weight-bold text-white">Password:</label>
                                                        <input className="form-control text-monospace" type="password" value={password} onChange={
                                                            (e)=>setPassword (e.target.value)} required/>
                                                    </div>
                                                </form>

                                                <div className="d-flex flex-row justify-content-between align-items-center">
                                                    <button className="btn btn-sm btn-success float-right px-5" type="button" disabled={loading} onClick={login}>
                                                        <span className="font-weight-bold">
                                                            {loading ? 'Authenticating' : 'Login'}
                                                        </span>
                                                    </button>

                                                    <a className="small text-white font-weight-bold" href="">
                                                        Forgot Password?</a>
                                                </div>
                                            </div>

                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>

                        <div className="card-footer bg-light">
                            <div className="text-midnight text-center font-weight-bold small">
                                <span>Copyright &copy; 2020 - Laboratorium Termal dan Kondisi Lingkungan</span>
                            </div>
                        </div>

                    </div>
                </div>

            </div>
        </div>
    );
};

export default Auth;