import React from 'react';
import ApexChartGauge from '../components/charts/sc-apexChart-gauge';
import ApexChartLine from '../components/charts/sc-apexChart-line';

import Card from '../components/sc-card';
import Nav from '../components/sc-nav';
import {error} from './sc-error';

import requestSensor from '../assets/js/utils/AxiosRequest';

function Dashboard() {
    return (
        <div className="container-fluid">
            {/* <!-- Content Row --> */}
            <div className="d-flex flex-column mb-3">

                <div className="d-flex align-items-center justify-content-between mb-3">
                    <div className="d-none d-sm-inline h3 text-dark my-auto">Dashboard</div>
                    <div className="d-none d-sm-inline">
                        <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button" onClick={requestSensor}>
                            <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                            <span className="text px-4">Generate Report</span>
                        </button>
                    </div>
                </div>

                <div className="row align-items-top mb-2">
                    <div className="d-none d-sm-block col-md-12 col-xl-4">
                        <div className="card border-left-midnight shadow py-2 mb-2">
                            <div className="card-body py-2">
                                <div className="d-block small font-weight-bold text-midnight px-1">
                                    Relative Humidity</div>
                                <ApexChartGauge chartLabel="Percent" chartHeight={320}/>
                            </div>
                        </div>
                    </div>

                    <div className="d-none d-sm-block col-md-12 col-xl-8">
                        <div className="row">
                            <div className="col-md-12 col-xl-6 mb-2">
                                <Card header="Evaporator Coil" value="12.76"/></div>
                            <div className="col-md-12 col-xl-6 mb-2">
                                <Card header="Leaving Evaporator" value="15.77"/></div>
                        </div>
                        
                        <div className="row">
                            <div className="col-md-12 col-xl-6 mb-2">
                                <Card header="Cabinet: Supply" value="22.31"/></div>
                            <div className="col-md-12 col-xl-6 mb-2">
                                <Card header="Cabinet: Return" value="29.98"/></div>
                        </div>

                        <div className="d-block dropdown-divider mb-2"></div>
                        <div className="d-block float-right text-dark small my-1">
                            <strong className="text-midnight">Last Updated:</strong> 09:27:42 PM
                        </div>
                    </div>
                </div>

                <div className="d-none d-sm-block card shadow">
                    <div className="card-body pb-0">
                        <div className="d-none d-sm-block">
                            <ApexChartLine chartLabel="Line Chart" chartHeight={320}/>
                        </div>
                    </div>
                </div>
            </div>

            {/* <!-- Datatable Tabs --> */}
            <div className="d-none d-md-block mb-3"><Nav /></div>
            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">{error ('Mobile view is not supported yet :(')}</div>
        </div>
    );
};

export default Dashboard;