/* *Local Storage Service */
import CryptoJS from 'crypto-js';
const SECRET_KEY = 'Oc4539yCAo9B1mM-75N6yRM2quXZsLQf6kgV0PSuwKo';

const AES = (function() {
    return ({
        encrypt:(item) => {return CryptoJS.AES.encrypt(item, SECRET_KEY).toString()},
        decrypt_utf8:(item) => {
            return CryptoJS.AES.decrypt(item, SECRET_KEY).toString(
                CryptoJS.enc.Utf8);
        }
    });
})();

const key = {
    refreshToken: CryptoJS.SHA256('refreshToken').toString(
        CryptoJS.enc.Base64),
    accessToken: CryptoJS.SHA256('accessToken').toString(
        CryptoJS.enc.Base64),
    user: CryptoJS.SHA256('user').toString(
        CryptoJS.enc.Base64)
};

function get(key) {
    return (localStorage.getItem(key)!=null ? AES.decrypt_utf8(
        localStorage.getItem(key)) : null);
};

const LocalStorageService = (function() {
    return ({
        setRefreshToken :refreshToken => localStorage.setItem(key.refreshToken, AES.encrypt(refreshToken)),
        setAccessToken  :accessToken => localStorage.setItem(key.accessToken, AES.encrypt(accessToken)),
        setUser         :user => localStorage.setItem(key.user, AES.encrypt(user)),
        clear           :() => localStorage.clear(),

        getAccessToken  :() => {return get(key.accessToken)},
        getRefreshToken :() => {return get(key.refreshToken)},
        isAuthenticated :() => {return get(key.accessToken)!==null},
        getUser         :() => {return get(key.user)}
    });
})();

export default LocalStorageService;